@import url(https://fonts.googleapis.com/css2?family=Zen+Kurenaido&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;

  background-color: #fff6df !important;
  background-image: linear-gradient(0deg, #fff6df 0%, #fffcf4 50%, #f8eedb 100%);

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/*code for custom scrollbar starts here*/
::-webkit-scrollbar {
  width: 5px;
}

::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey; 
  border-radius: 10px;
}
 
::-webkit-scrollbar-thumb {
  background: #4e4752; 
  border-radius: 10px;
}
::-webkit-scrollbar-thumb:hover {
  background: #4e4752; 
}
/*code for custom scrollbar ends here*/

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.heading{
    font-family: 'Zen Kurenaido', sans-serif;
    font-weight: bold;
    color: #74340f;
}
.sub-heading{
    font-family: 'Zen Kurenaido', sans-serif;
}

.img-logo{
    height: 100px;
}
.card-room-entry{
    width:350px;
    border-radius: 10px;
    border: 1px solid rgb(0, 0, 0);
    padding: 10px;
    margin: 10px;
    box-shadow:5px 5px 7px black;
    background-color: white;
}
.card-room-entry:hover{
    box-shadow:5px 5px 10px black;
}
.card-room-title{
    color:#74340f;
    font-family: 'Zen Kurenaido', sans-serif;
    font-weight: bold;
}
.img-room-entry{
    height:250px !important;
}

.btn-room-entry{
    width:100%;
    font-size: 20px;
    background-color:#faf3e6;
    background-image: linear-gradient(0deg, #fff6df 0%, #fffcf4 50%, #f8eedb 100%);
    padding: 8px;
    border-radius: 0.5rem;
    margin-top: 17px;
    color: #74340f;
    box-shadow: 2px 2px 2px #d0bfaa;
    border:1px solid #d0bfaa;
}

.btn-room-entry:hover{
    box-shadow: 2px 2px 2px #d0bfaa;
    border:1px dotted #74340f;
    background-color:#faf3e6;
}

.footer{
    position: relative;
    bottom: 0;
    width: 100%;
    height: 120px;
    background-color:rgb(255, 255, 255);
}
.footer-text{
    font-size: 20px;
    padding: 10px;
    width: 100%;
}
.title{
    color:#74340f;
    font-family: 'Zen Kurenaido', sans-serif;
    font-weight: bold;
}
.title img{
     height:70px;
}

.codeEditor{
    width: 100%;
    height: 80vh;
    border: 1px solid #ccc;
    border-radius: 5px;
    padding: 10px;
    font-family: monospace;
    font-size: 20px;
    background-color: #fafafa;
    margin:5px;
}

.meeting-info{
    padding:5px;
    border: 1px solid rgb(0, 0, 0);
    margin: 5px;
    padding: 5px;
    border-radius: 5px;
    background-color: #fafafa;

}



.chat-window{
    background-color: #8EC5FC;
    background-image: linear-gradient(62deg, #8EC5FC 0%, #E0C3FC 100%);
    
}
.chat-container{
    height:500px;
    overflow-y: auto;
    background-image: url(/static/media/chat-bg.bded4f44.png);
    padding:5px;
}

.message{
    border: 1px solid #ccc;
    margin-top:15px;
    padding:10px;
    background-color: #fafafa;
    min-width: 200px;
}

.message.left{
    float: left;
    clear: both;
    border-radius: 20px 20px 20px 0;
}

.message.right{
    float: right;
    clear: both;
    border-radius: 20px 20px  0 20px;
}

.message-sticker{

}

.message-sticker.left{
    float: left;
    clear: both;
    border-radius: 20px 20px 20px 0;
}

.message-sticker.right{
    float: right;
    clear: both;
    border-radius: 20px 20px  0 20px;
}

.btn-send-message{
   border:none;
   font-size: 20px;
   padding: 10px;
}

.chat-sticker{
    height:60px;
    width:60px;
    background-color: rgb(255, 255, 255);
    padding: 5px;
    border-radius:50%;
    box-shadow: 0px 0px 5px rgb(0, 0, 0);
}

.chat-message-sticker{
    height:100px;
    width:100px;
}

.container-stickers{
    padding: 10px;
}

.member-name{
    background-color: white;
    padding: 5px;
    font-size:10px;
    border-radius: 15px;
    font-size: 20px;
    padding-left: 10px;
    padding-right: 10px;
}
